import * as Rx from 'rxjs';
let isActive = false;
const loadingCount$ = new Rx.BehaviorSubject(0);
export const initLoadingIndicator = addLoadingCount => addLoadingCount(loadingCount$);
export const loadingIndicator = {
  show: () => {
    if (!isActive) {
      isActive = true;
      loadingCount$.next(1);
    }
  },
  hide: () => {
    if (isActive) {
      isActive = false;
      loadingCount$.next(0);
    }
  }
};