import { sortBy } from 'lodash';
import { getFunctionHelp } from '../../../i18n';
export function sort() {
  const {
    help,
    args: argHelp
  } = getFunctionHelp().sort;
  return {
    name: 'sort',
    type: 'datatable',
    inputTypes: ['datatable'],
    help,
    args: {
      by: {
        types: ['string'],
        aliases: ['_', 'column'],
        multi: false,
        // TODO: No reason you couldn't.
        help: argHelp.by
      },
      reverse: {
        types: ['boolean'],
        help: argHelp.reverse,
        options: [true, false],
        default: false
      }
    },
    fn: (input, args) => {
      const column = args.by || input.columns[0].name;
      return { ...input,
        rows: args.reverse ? sortBy(input.rows, column).reverse() : sortBy(input.rows, column)
      };
    }
  };
}