import { getFunctionHelp } from '../../../i18n';

const noop = () => {};

export function location() {
  const {
    help
  } = getFunctionHelp().location;
  return {
    name: 'location',
    type: 'datatable',
    inputTypes: ['null'],
    args: {},
    help,
    fn: () => {
      return new Promise(resolve => {
        function createLocation(geoposition) {
          const {
            latitude,
            longitude
          } = geoposition.coords;
          return resolve({
            type: 'datatable',
            columns: [{
              id: 'latitude',
              name: 'latitude',
              meta: {
                type: 'number'
              }
            }, {
              id: 'longitude',
              name: 'longitude',
              meta: {
                type: 'number'
              }
            }],
            rows: [{
              latitude,
              longitude
            }]
          });
        }

        return navigator.geolocation.getCurrentPosition(createLocation, noop, {
          maximumAge: 5000
        });
      });
    }
  };
}