import { getType } from '../../../types';
import { getFunctionHelp } from '../../../i18n';
export function asFn() {
  const {
    help,
    args: argHelp
  } = getFunctionHelp().as;
  return {
    name: 'as',
    type: 'datatable',
    inputTypes: ['string', 'boolean', 'number', 'null'],
    help,
    args: {
      name: {
        types: ['string'],
        aliases: ['_'],
        help: argHelp.name,
        default: 'value'
      }
    },
    fn: (input, args) => {
      return {
        type: 'datatable',
        columns: [{
          id: args.name,
          name: args.name,
          meta: {
            type: getType(input)
          }
        }],
        rows: [{
          [args.name]: input
        }]
      };
    }
  };
}