import { EmbeddableTypes, EmbeddableExpressionType } from '../../expression_types';
import { buildEmbeddableFilters } from '../../../public/lib/build_embeddable_filters';
import { getFunctionHelp } from '../../../i18n';
export function savedSearch() {
  const {
    help,
    args: argHelp
  } = getFunctionHelp().savedSearch;
  return {
    name: 'savedSearch',
    help,
    args: {
      id: {
        types: ['string'],
        required: false,
        help: argHelp.id
      }
    },
    type: EmbeddableExpressionType,
    fn: (input, {
      id
    }) => {
      const filters = input ? input.and : [];
      return {
        type: EmbeddableExpressionType,
        input: {
          id,
          ...buildEmbeddableFilters(filters)
        },
        embeddableType: EmbeddableTypes.search,
        generatedAt: Date.now()
      };
    }
  };
}