import { toExpression as toExpressionString } from '@kbn/interpreter/common';
export function toExpression(input, palettes) {
  const expressionParts = [];
  expressionParts.push('savedLens');
  expressionParts.push(`id="${input.id}"`);

  if (input.title !== undefined) {
    expressionParts.push(`title="${input.title}"`);
  }

  if (input.timeRange) {
    expressionParts.push(`timerange={timerange from="${input.timeRange.from}" to="${input.timeRange.to}"}`);
  }

  if (input.palette) {
    expressionParts.push(`palette={${toExpressionString(palettes.get(input.palette.name).toExpression(input.palette.params))}}`);
  }

  return expressionParts.join(' ');
}