import { EmbeddableTypes, EmbeddableExpressionType } from '../../expression_types';
import { getQueryFilters } from '../../../public/lib/build_embeddable_filters';
import { getFunctionHelp } from '../../../i18n';
const defaultTimeRange = {
  from: 'now-15m',
  to: 'now'
};
export function savedVisualization() {
  const {
    help,
    args: argHelp
  } = getFunctionHelp().savedVisualization;
  return {
    name: 'savedVisualization',
    help,
    args: {
      id: {
        types: ['string'],
        required: false,
        help: argHelp.id
      },
      timerange: {
        types: ['timerange'],
        help: argHelp.timerange,
        required: false
      },
      colors: {
        types: ['seriesStyle'],
        help: argHelp.colors,
        multi: true,
        required: false
      },
      hideLegend: {
        types: ['boolean'],
        help: argHelp.hideLegend,
        required: false
      },
      title: {
        types: ['string'],
        help: argHelp.title,
        required: false
      }
    },
    type: EmbeddableExpressionType,
    fn: (input, {
      id,
      timerange,
      colors,
      hideLegend,
      title
    }) => {
      const filters = input ? input.and : [];
      const visOptions = {};

      if (colors) {
        visOptions.colors = colors.reduce((reduction, color) => {
          if (color.label && color.color) {
            reduction[color.label] = color.color;
          }

          return reduction;
        }, {});
      }

      if (hideLegend === true) {
        // @ts-expect-error LegendOpen missing on VisualizeInput
        visOptions.legendOpen = false;
      }

      return {
        type: EmbeddableExpressionType,
        input: {
          id,
          disableTriggers: true,
          timeRange: timerange || defaultTimeRange,
          filters: getQueryFilters(filters),
          vis: visOptions,
          title: title === null ? undefined : title
        },
        embeddableType: EmbeddableTypes.visualization,
        generatedAt: Date.now()
      };
    }
  };
}